<template>
   <div class="movie_body" ref="movie_body">
        <Loading v-if="isLoading"></Loading>
            <scroller v-else :handleToScroll = "handleToScroll"  :hendleToTouchEnd = "hendleToTouchEnd">
                <ul>
                    <!-- 一些模拟的假数据 -->
                    <li class="pullDown">{{pullDownMsg}}</li>
                    <li v-for="item in nowPlayList" :key="item.id">
                        <div class="pic_show"><img :src="item.img | setWH('128.180')" @tap="gotoDetail(item.id)"></div>
                        <div class="info_list">
                            <h2>{{item.nm}} <img src="@/assets/maxs.png" alt="" v-if="item.version" @tap="gotoDetail(item.id)"></h2>
                            <p>观众评 <span class="grade">{{item.sc}}</span></p>
                            <p>主演: {{item.star}}</p>
                            <p>{{item.showInfo}}</p>
                        </div>
                        <div class="btn_mall">
                            购票
                        </div>
                    </li>
                </ul>
            </scroller>
    </div>
</template>
<script>
// import BScroll from "better-scroll";
import { setTimeout } from 'timers';
import { MessageBox } from 'mint-ui';
export default {
    name: "nowplaying",
    data: function(){
        return {
            nowPlayList: [],
            pullDownMsg: "",
            isLoading: true,
            preID:-1,
        }
    },
    methods:{
        gotoDetail: function(movieID){
            // console.log("gotoDetail");
            this.$router.push("/movie/detail/1/" + movieID);
        },
        handleToScroll: function(pos){
            // 这里的pos使从子组件传递过来的
            var _this = this;
            if(pos.y > 30){
                _this.pullDownMsg = "正在更新中";
            }
        },
        hendleToTouchEnd: function(pos){
            var _this = this;
            if(pos.y > 30){
                // console.log("okkk");
                
                _this.$http.get("http://39.97.33.178/api/movieOnInfoList?cityid=13").then(function(res){
                    _this.pullDownMsg = "更新成功";
                    setTimeout(function(){
                        if(res.data.status === 0){
                            _this.nowPlayList = res.data.data.movieList;
                            _this.pullDownMsg = "";
                            }
                        }, 1000) 
                })
            }
        },
    },
    activated: function(){
        // console.log("123");
        var cityID = this.$store.state.city.id;
        if(this.preID === cityID){return;}
        // 如果id相等会会执行该句，会出加载更多
        this.isLoading = true;
        var _this = this;
        this.$http.get("http://39.97.33.178/api/movieOnInfoList?cityId="+cityID).then(function(res){
            // console.log(res.data.data)
            if(res.data.status === 0){
                _this.nowPlayList = res.data.data.movieList;
                _this.isLoading = false;
                _this.preID = cityID;
                // console.log(_this.nowPlayList);
                // _this.$nextTick(function(){
                //     var scroll = new BScroll(this.$refs.movie_body, { // 自定义事件
                //         tap : true,
                //         probeType: 1, // 设置函数节流， 因为滑动的时候会频繁调用回调，所以这里设置其模式是节流的
                //     });
                    
                //     scroll.on("scroll", function(pos){
                //         // 一个类似下滑刷新的额功能
                //         // console.log("下滑刷新"); 
                //         //  pos是一个对象，实时包括着滑动的x和y的距离， 所以当大于30的时候可以做一个刷新的效果
                //         if(pos.y > 30){
                //             _this.pullDownMsg = "正在更新中";
                //              _this.$http.get("/api/movieOnInfoList?cityid=13").then(function(res){
                //                 _this.pullDownMsg = "更新成功";
                //                 setTimeout(function(){
                //                     if(res.data.status === 0){
                //                         _this.nowPlayList = res.data.data.movieList;
                //                         _this.pullDownMsg = "";
                //                     }
                //                 }, 1000)
                                 
                //             })
                //         }

                //     });
                //     // scroll.on("touchEnd", function(pos){
                //     //     // 滑动结束之后触发 
                //     //     // console.log("滑动结束");
                //     //     //  同样的 Pos 也是一个实时接收坐标的参数
                //     //     if(pos.y > 30){
                //     //         _this.$http.get("/api/movieOnInfoList?cityid=13").then(function(res){
                //     //             _this.pullDownMsg = "更新成功";
                //     //             setTimeout(function(){
                //     //                 if(res.data.status === 0){
                //     //                     _this.nowPlayList = res.data.data.movieList;
                //     //                     _this.pullDownMsg = "";
                //     //                 }
                //     //             }, 1000)
                                 
                //     //         })
                //     //     }
                //     // });
                //     //  这样写的时候会有一个问题，下滑的时候会出现不显示正在更新，直接显示更新成功的问题，所以将下滑刷新的功能放在了scroll中。
                // })
            }
        })
    },


}
</script>
<style lang="scss" scoped>
    #content .movie_body{ flex:1; overflow:auto;}
    .movie_body ul{ margin:0 12px; overflow: hidden;}
    .movie_body ul li{ margin-top:12px; display: flex; align-items:center; border-bottom: 1px #e6e6e6 solid; padding-bottom: 10px;}
    .movie_body .pic_show{ width:64px; height: 90px;}
    .movie_body .pic_show img{ width:100%;}
    .movie_body .info_list { margin-left: 10px; flex:1; position: relative;}
    .movie_body .info_list h2{ font-size: 17px; line-height: 24px; width:150px; overflow: hidden; white-space: nowrap; text-overflow:ellipsis;}
    .movie_body .info_list p{ font-size: 13px; color:#666; line-height: 22px; width:200px; overflow: hidden; white-space: nowrap; text-overflow:ellipsis;}
    .movie_body .info_list .grade{ font-weight: 700; color: #faaf00; font-size: 15px;}
    .movie_body .info_list img{ width:50px; position: absolute; right:10px; top: 5px;}
    .movie_body .btn_mall , .movie_body .btn_pre{ width:47px; height:27px; line-height: 28px; text-align: center; background-color: #f03d37; color: #fff; border-radius: 4px; font-size: 12px; cursor: pointer;}
    .movie_body .btn_pre{ background-color: #3c9fe6;}
    .movie_body .pullDown{ margin:0; padding:0; border:none;}
</style>
